import { NextRequest, NextResponse } from 'next/server';
import { clientConfirmPayment, getPaymentConfirmationByToken } from '@/services/paymentService';
import { handleApiError } from '@/utils/error';
import { logApiRequest, logApiResponse } from '@/utils/logger';
import { z } from 'zod';

export const dynamic = 'force-dynamic';

/**
 * Client confirms payment (no auth required - uses token)
 * POST /api/payment-confirmation
 */
export async function POST(req: NextRequest): Promise<NextResponse> {
  const startTime = Date.now();

  try {
    logApiRequest('POST', '/api/payment-confirmation');

    // Parse and validate request body
    const body = await req.json();

    const schema = z.object({
      confirmationToken: z.string().uuid(),
      amount: z.number().positive(),
      paymentMethod: z.enum(['bank_transfer', 'card']),
      datePaid: z.string().refine((date) => !isNaN(Date.parse(date)), {
        message: 'Invalid date format',
      }),
      clientNotes: z.string().optional(),
    });

    const validated = schema.parse(body);

    // Confirm payment
    const confirmation = await clientConfirmPayment(
      validated.confirmationToken,
      validated.amount,
      validated.paymentMethod,
      validated.datePaid,
      validated.clientNotes
    );

    // Log response
    const duration = Date.now() - startTime;
    logApiResponse('POST', '/api/payment-confirmation', 200, duration);

    return NextResponse.json({
      success: true,
      message: 'Payment confirmed successfully. The freelancer will be notified to verify receipt.',
      confirmation: {
        confirmationId: confirmation.confirmationId,
        status: confirmation.status,
        clientConfirmedAt: confirmation.clientConfirmedAt,
      },
    });
  } catch (error) {
    const duration = Date.now() - startTime;
    logApiResponse('POST', '/api/payment-confirmation', 500, duration);
    return handleApiError(error);
  }
}

/**
 * Get payment confirmation details by token (no auth required)
 * GET /api/payment-confirmation?token=xxx
 */
export async function GET(req: NextRequest): Promise<NextResponse> {
  const startTime = Date.now();

  try {
    logApiRequest('GET', '/api/payment-confirmation');

    const { searchParams } = new URL(req.url);
    const token = searchParams.get('token');

    if (!token) {
      return NextResponse.json(
        { error: 'MISSING_TOKEN', message: 'Confirmation token is required' },
        { status: 400 }
      );
    }

    // Validate token format
    const uuidRegex = /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i;
    if (!uuidRegex.test(token)) {
      return NextResponse.json(
        { error: 'INVALID_TOKEN', message: 'Invalid confirmation token format' },
        { status: 400 }
      );
    }

    // Get confirmation
    const confirmation = await getPaymentConfirmationByToken(token);

    // Log response
    const duration = Date.now() - startTime;
    logApiResponse('GET', '/api/payment-confirmation', 200, duration);

    return NextResponse.json({
      confirmation: {
        confirmationId: confirmation.confirmationId,
        expectedAmount: confirmation.expectedAmount,
        status: confirmation.status,
        clientEmail: confirmation.clientEmail,
        invoiceId: confirmation.invoiceId,
        expiresAt: confirmation.expiresAt.toDate().toISOString(),
      },
    });
  } catch (error) {
    const duration = Date.now() - startTime;
    logApiResponse('GET', '/api/payment-confirmation', 500, duration);
    return handleApiError(error);
  }
}
